import React from "react";
import { useNavigate } from "react-router-dom";
import "./page4.css";

const Page4 = ({ predictionResult }) => {
  const navigate = useNavigate();

  const handleNext = () => {
    navigate("/page5");
  };

  const handlePrevious = () => {
    navigate("/page3");
  };

  if (!predictionResult) {
    return <p>  ...</p>;
  }

  const renderContent = () => {
    switch (predictionResult.class) {
      case "Oval":
        return {
          subtext:
            "           .",
          options: [
            { src: "/assets/.png", alt: "", name: "" },
            { src: "/assets/.jpg", alt: "", name: "" },
          ],
        };
      case "Round":
        return {
          subtext:
            "       ,     . ",
          options: [
            { src: "/assets/.jpg", alt: "", name: "" },
            { src: "/assets/.png", alt: "", name: "" },
          ],
        };
      case "Heart":
        return {
          subtext:
            "     ,    .",
          options: [
            { src: "/assets/.png", alt: "", name: "" },
            { src: "/assets/.png", alt: "", name: "" },
          ],
        };
      case "Square":
        return {
          subtext:
            "       ,    .",
          options: [
            { src: "/assets/.png", alt: "", name: "" },
            { src: "/assets/.png", alt: "", name: "" },
          ],
        };
      default:
        return {
          subtext: "   .",
          options: [],
        };
    }
  };

  const { subtext, options } = renderContent();

  return (
    <div className="container">
      <div className="progress-bar">
        <div className="progress" style={{ width: `${(3 / 4) * 100}%` }}></div>
      </div>
      <h1>
         {" "}
        <span id="result-bold">
          {predictionResult.class}
        </span>
        . ({(predictionResult.confidence * 100).toFixed(2)}%)
      </h1>
      <p className="subtext">{subtext}</p>
      <div className="options">
        {options.map((option, index) => (
          <div className="option" key={index}>
            <img src={option.src} alt={option.alt} />
            <p className="subtext-context">{option.name}</p>
          </div>
        ))}
      </div>
      <div className="navigation">
        <button className="prev-button" onClick={handlePrevious}>
          
        </button>
        <button className="next-button" onClick={handleNext}>
          
        </button>
      </div>
    </div>
  );
};

export default Page4;